import "../CSS/Desctop.css"

import DesctopLogo  from '../../../Images/workspace-avatar.png';
import IconEdit  from '../../../Images/icon-edit.png';

import { useEffect, useRef, useState } from "react";
import url from "../../../config.js"
import React from 'react';

import { useQuery, useMutation } from '@apollo/client'

function DesctopPart() {
    const [rightWidth, setRightWidth] = useState(window.innerWidth - 255);

    useEffect(() => {
        const handleResize = () => {
            setRightWidth(window.innerWidth - 255);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightHeight, setRightHeight] = useState(window.innerHeight - 100);

    useEffect(() => {
        const handleResize = () => {
            setRightHeight(window.innerHeight - 100);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [darkMode, setDarkMode] = useState(false);

    useEffect(() => {
        const storedValue = JSON.parse(localStorage.getItem("theme"));
        if (storedValue !== null) {
        setDarkMode(storedValue);
        }
    }, []);

    const chooseDark = (event) => {
        const newValue = event.target.checked;
        setDarkMode(newValue);
        localStorage.setItem("theme", JSON.stringify(newValue));
    };

    const [powerMode, setPowerMode] = useState(false);

    useEffect(() => {
        const storedValue = JSON.parse(localStorage.getItem("powerMode"));
        if (storedValue !== null) {
        setPowerMode(storedValue);
        }
    }, []);

    const choosePower = (event) => {
        const newValue = event.target.checked;
        setPowerMode(newValue);
        localStorage.setItem("powerMode", JSON.stringify(newValue));
    };

    const [saveMode, setSaveMode] = useState(false);

    useEffect(() => {
        const storedValue = JSON.parse(localStorage.getItem("saveMode"));
        if (storedValue !== null) {
        setSaveMode(storedValue);
        }
    }, []);

    const chooseSave = (event) => {
        const newValue = event.target.checked;
        setSaveMode(newValue);
        localStorage.setItem("saveMode", JSON.stringify(newValue));
    };
    
    return (
        <div className="rendered-area desktop-render" style={{width: rightWidth, height: rightHeight}}>
            <div className="desktop-render-top">
                <img src={DesctopLogo} alt="profile" />
                <h1>{JSON.parse(localStorage.getItem('userInfo')).surname} {JSON.parse(localStorage.getItem('userInfo')).name}</h1>
                <button><img src={IconEdit} alt="edit" /></button>
            </div>
            <div className="desktop-render-mid">
                <h1>Informatsiya</h1>
                <div>
                    <p>Status: {JSON.parse(localStorage.getItem('userInfo')).status}</p>
                    <p>Token: {JSON.parse(localStorage.getItem('userInfo')).token}</p>
                </div>
            </div>
            <div className="desktop-render-bottom">
                <h1>Sozlamalar</h1>
                <div className="desktop-render-bottom-box">
                    <h2>Qorong'i rejim</h2>
                    <label class="switch">
                        <input
                            type="checkbox"
                            onChange={chooseDark}
                            checked={darkMode}
                        />
                        <span class="slider round"></span>
                    </label>
                </div>
                <div className="desktop-render-bottom-box">
                    <h2>Quvvatni tejash rejimi</h2>
                    <label class="switch">
                        <input
                            type="checkbox"
                            onChange={choosePower}
                            checked={powerMode}
                        />
                        <span class="slider round"></span>
                    </label>
                </div>
                <div className="desktop-render-bottom-box">
                    <h2>Foydalanuvchini saqlash</h2>
                    <label class="switch">
                        <input
                            type="checkbox"
                            onChange={chooseSave}
                            checked={saveMode}
                        />
                        <span class="slider round"></span>
                    </label>
                </div>
            </div>
        </div>
    );
}

export default DesctopPart;
  