import "../CSS/Schedule.css"

import { useEffect, useRef, useState } from "react";
import url from "../../../config.js"
import React from 'react';

import { useQuery, useMutation } from '@apollo/client'

function SchedulePart() {
    const [rightWidth, setRightWidth] = useState(window.innerWidth - 255);

    useEffect(() => {
        const handleResize = () => {
            setRightWidth(window.innerWidth - 255);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightHeight, setRightHeight] = useState(window.innerHeight - 100);

    useEffect(() => {
        const handleResize = () => {
            setRightHeight(window.innerHeight - 100);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    let [schedule, setSchedule] = useState();

    useEffect(() => {
        const getSchedule = async() => {
                try {
                    const response = await fetch('http://worldtimeapi.org/api/timezone/Etc/UTC');
                    const data = await response.json();
                    const currentDateTime = new Date(data.datetime);
        
                    currentDateTime.setUTCHours(0, 0, 0, 0);
                    const startOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
                    fetch(url + "/get_daily_schedule", {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json' 
                        },
                        body: JSON.stringify({
                            startOfDayFormatted: startOfDays.slice(0,10),
                            token: JSON.parse(localStorage.getItem("userInfo")).token
                    })
                    })
                    .then(res => res.json())
                    .then(data => {
                        setSchedule(data)
                    })
                }catch (error) {
                console.error('Error fetching current day times:', error);
            }
        }
        getSchedule()
    }, [])

    console.log(schedule)

    return (
        <div className="rendered-area schedule-render" style={{width: rightWidth, height: rightHeight}}>
            Schedule
        </div>
    );
}

export default SchedulePart;
  