import "./CSS/Admin.css"

import { useState, useEffect } from "react";
import React from 'react';

import Patients from "./Components/Patients";
import DesctopPart from "./Components/Desctop";
import Service from "./Components/Service";
import Statistics from "./Components/Statistics";

import Emoji from "../../Images/emoji.png"

import HomeIcon from "../../Images/file.png"
import PatientIcon from "../../Images/boy.png"
import ServiceIcon from "../../Images/notebook.png"
import StatisticIcon from "../../Images/chart.png"
import ScheduleIcon from "../../Images/Calendar.png"
import LogOut from "../../Images/phone.png"

import WhiteHomeIcon from "../../Images/whitefile.png"
import WhitePatientIcon from "../../Images/whiteboy.png"
import WhiteServiceIcon from "../../Images/whitenotebook.png"
import WhiteStatisticIcon from "../../Images/whitechart.png"
import WhiteScheduleIcon from "../../Images/whitecalendar.png"
import WhiteLogOut from "../../Images/whitephone.png"
import SchedulePart from "./Components/Schedule";

import { useNavigate } from 'react-router-dom';

function Admin() {
    const navigate = useNavigate();
    const handleCheckUserInfo = () => {
        const userInfo = JSON.parse(localStorage.getItem('userInfo'));
        if(userInfo != null){
            if(userInfo.status == 'SUPER_ADMIN'){
                    console.log("OK")
            }else if(userInfo.status == 'ADMIN'){
                console.log("OK")
            }
        }else {
            navigate('/login');
        }
    }
    handleCheckUserInfo()

    const [rightHeight, setRightHeight] = useState(window.innerHeight);

    useEffect(() => {
        const handleResize = () => {
            setRightHeight(window.innerHeight);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightWidth, setRightWidth] = useState(window.innerWidth - 255);

    useEffect(() => {
        const handleResize = () => {
            setRightWidth(window.innerWidth - 255);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    //Right Size Manager Ends

    const [desktopLink, setDesktopLink] = useState(false);
    const [patientLink, setPatientLink] = useState(false);
    const [serviceLink, setServiceLink] = useState(false);
    const [scheduleLink, setScheduleLink] = useState(false);
    const [statisticsList, setStatisticsList] = useState(false);

    function selectDesctop(){
        localStorage.removeItem("status")
        localStorage.setItem("status", "desktop")
        setDesktopLink(desktopLink ? false : true)
        setPatientLink(false)
        setServiceLink(false)
        setStatisticsList(false)
        setScheduleLink(false)
    }

    function selectPatients(){
        localStorage.removeItem("status")
        localStorage.setItem("status", "patients")
        setDesktopLink(false)
        setPatientLink(patientLink ? false : true)
        setServiceLink(false)
        setStatisticsList(false)
        setScheduleLink(false)
    }

    function selectService(){
        localStorage.removeItem("status")
        localStorage.setItem("status", "service")
        setDesktopLink(false)
        setPatientLink(false)
        setServiceLink(serviceLink ? false : true)
        setStatisticsList(false)
        setScheduleLink(false)
    }

    function selectSchedule(){
        localStorage.removeItem("status")
        localStorage.setItem("status", "schedule")
        setDesktopLink(false)
        setPatientLink(false)
        setServiceLink(false)
        setStatisticsList(false)
        setScheduleLink(scheduleLink ? false : true)
    }

    function selectStatistics(){
        localStorage.removeItem("status")
        localStorage.setItem("status", "statistics")
        setDesktopLink(false)
        setPatientLink(false)
        setServiceLink(false)
        setStatisticsList(statisticsList ? false : true)
        setScheduleLink(false)
    }

    function logOut(){
        localStorage.clear()
        window.location = "/login"
    } 

    return (
       <div className="adminBody" style={{height: rightHeight}}>
        <div className="left" style={{height: rightHeight, width: "255px"}}>
            <div className="left-box" style={{height: rightHeight, width: "255px"}}>
            <div className="left_top">
                <h1>Admin</h1>
            </div>
            <div className="left_bottom">
                <button className={desktopLink ? 'left_link_active' : 'left_link'} onClick={() => selectDesctop()}>
                    <img src={desktopLink ? HomeIcon : WhiteHomeIcon} alt="icon" className="nav-icon"/>
                    <h1>Kabinet</h1>
                </button>
                <button className={patientLink ? 'left_link_active' : 'left_link'} onClick={() => selectPatients()}>
                    <img src={patientLink ? PatientIcon : WhitePatientIcon} alt="icon" className="nav-icon"/>
                    <h1>Bemorlar</h1>
                </button>
                <button className={serviceLink ? 'left_link_active' : 'left_link'} onClick={() => selectService()}>
                    <img src={serviceLink ? ServiceIcon : WhiteServiceIcon} alt="icon" className="nav-icon"/>
                    <h1>Xizmatlar</h1>
                </button>
                <button className={scheduleLink ? 'left_link_active' : 'left_link'} onClick={() => selectSchedule()}>
                    <img src={scheduleLink ? ScheduleIcon : WhiteScheduleIcon} alt="icon" className="nav-icon"/>
                    <h1>Jadval</h1>
                </button>
                <button className={statisticsList ? 'left_link_active' : 'left_link'} onClick={() => selectStatistics()}>
                    <img src={statisticsList ? StatisticIcon : WhiteStatisticIcon} alt="icon" className="nav-icon"/>
                    <h1>Statistika</h1>
                </button>
                <button className={'left_link'} style={{"marginTop": "50px"}} onClick={() => logOut()}>
                    <img src={WhiteLogOut} alt="icon" className="nav-icon"/>
                    <h1 style={{ color:'#A4A6B3' }}>Chiqish</h1>
                </button>
            </div>
            </div>
        </div>
        <div className="right" style={{width: `${rightWidth}px`, height: `${rightHeight}px`}}>
            <div className="navbar">
                <h1 className="navbar-title">Salom, {JSON.parse(localStorage.getItem("userInfo")).name} <img src={Emoji} alt="" /> </h1>
                <div>
                    <input type="text" placeholder="Search"/>
                    <img src="https://img.icons8.com/3d-fluency/94/user-male-circle.png" alt="" />
                </div>
            </div>
            <div className="render" style={{ display: desktopLink ? 'block' : 'none'}}>
                <DesctopPart/>
            </div>
            <div className="render" style={{ display: patientLink ? 'block' : 'none'}}> 
                <Patients/>
            </div>
            <div className="render" style={{ display: serviceLink ? 'block' : 'none'}}>
                <Service/>
            </div>
            <div className="render" style={{ display: scheduleLink ? 'block' : 'none'}}>
                <SchedulePart />
            </div>
            <div className="render" style={{ display: statisticsList ? 'block' : 'none'}}>
                <Statistics/>
            </div>
        </div>
       </div>
    );
}

export default Admin;
  