import "../CSS/Patients.css"

import { useEffect, useRef, useState } from "react";
import url from "../../../config.js"
import React from 'react';
import Select from 'react-select';

import { useQuery, useMutation } from '@apollo/client'
import { PATIENTS, REGISTER_DAILY_PATIENTS, UPDATE_DAILY_PATIENTS_STATUS, ADD_PATIENTS} from '../../../Query.js'

function Patients() {
    const [localDateTime, setLocalDateTime] = useState('');

    useEffect(() => {
        fetch('https://worldtimeapi.org/api/ip')
        .then(response => response.json())
        .then(data => {
            const datetime = data.datetime.substring(0, 23) + 'Z';
            setLocalDateTime(datetime);
        })
        .catch(error => {
            console.error('Error fetching local date and time:', error);
        });
    }, []);

    let [patientsTopWaiting, setPatientsTopWaiting] = useState(0);
    let [patientsTopProcess, setPatientsTopProcess] = useState(0);
    let [patientsTopDone, setPatientsTopDone] = useState(0);
    let [patientsRender, setPatientsRender] = useState(null);

    // const localStorageValue = localStorage.getItem('status');
    // if(localStorageValue == "patients") {
    useEffect(() => {
        const getPatients = async() => {
                try {
                    const response = await fetch('http://worldtimeapi.org/api/timezone/Etc/UTC');
                    const data = await response.json();
                    const currentDateTime = new Date(data.datetime);
        
                    currentDateTime.setUTCHours(0, 0, 0, 0);
                    const startOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');

                    currentDateTime.setUTCHours(23, 59, 0 ,0);
                    const endOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
                     fetch(url + "/get_monthly_patients", {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json' 
                        },
                        body: JSON.stringify({
                            startOfDayFormatted: startOfDays,
                            endOfDayFormatted: endOfDays,
                            token: JSON.parse(localStorage.getItem("userInfo")).token
                    })
                    })
                    .then(res => res.json())
                    .then(data => {
                        setPatientsRender(data)
                        let Waiting = 0;
                        let Process = 0;
                        let Done = 0;
                        for(let i = 0; i < data.length; i++){
                            if(data[i].status == "WAITING") {
                                Waiting++   
                            } else if(data[i].status == "PROCESS") {
                                Process++
                            } else if(data[i].status == "DONE") {
                                Done++  
                            } else {
                                console.log("Something went wrong!")
                            }
                        }
            
                        setPatientsTopWaiting(Waiting)
                        setPatientsTopProcess(Process)
                        setPatientsTopDone(Done)
                    })
                }catch (error) {
                console.error('Error fetching current day times:', error);
            }
        }
        getPatients()
    }, [])
    // }

    //////////////////////////////////////////////////////////////////////////////////////////////

    //Right Size Manager Starts

    const [rightWidth, setRightWidth] = useState(window.innerWidth - 255);

    useEffect(() => {
        const handleResize = () => {
            setRightWidth(window.innerWidth - 255);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightHeight, setRightHeight] = useState(window.innerHeight - 100);

    useEffect(() => {
        const handleResize = () => {
            setRightHeight(window.innerHeight -100);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightBottomHeight, setRightBottomHeight] = useState(window.innerHeight - 340);

    useEffect(() => {
        const handleResize = () => {
            setRightBottomHeight(window.innerHeight - 340);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [renderBottomBottomPatients, setRenderBottomBottomPatients] = useState(window.innerHeight - 463);

    useEffect(() => {
        const handleResize = () => {
            setRenderBottomBottomPatients(window.innerHeight - 463);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    //Right Size Manager Ends

    //////////////////////////////////////////////////////////////////////////////////////////////

    //Get current day and get start, end


    //Get current day and get start, end. End

    //////////////////////////////////////////////////////////////////////////////////////////////

    //Render Patients Start

    let renderAllList = useRef();
    let renderWaitingList = useRef();
    let renderProcessList = useRef();
    let renderDoneList = useRef();
    let renderSearchList = useRef();

    let [renderAllButton, setRenderAllButton] = useState(true);
    let [renderWaitingButton, setRenderWaitingButton] = useState(false);
    let [renderProcessButton, setRenderProcessButton] = useState(false);
    let [renderDoneButton, setRenderDoneButton] = useState(false);

    function renderAllTrigger(){
        renderSearchList.current.style.display = "none"
        setRenderAllButton(true)
        setRenderWaitingButton(false)
        setRenderProcessButton(false)
        setRenderDoneButton(false)
    }

    function renderWaitingTrigger(){
        renderSearchList.current.style.display = "none"
        setRenderAllButton(false)
        setRenderWaitingButton(true)
        setRenderProcessButton(false)
        setRenderDoneButton(false)
    }

    function renderProcessTrigger(){
        renderSearchList.current.style.display = "none"
        setRenderAllButton(false)
        setRenderWaitingButton(false)
        setRenderProcessButton(true)
        setRenderDoneButton(false)
    }

    function renderDoneTrigger(){
        renderSearchList.current.style.display = "none"
        setRenderAllButton(false)
        setRenderWaitingButton(false)
        setRenderProcessButton(false)
        setRenderDoneButton(true)
    }

    let searchInput = useRef();
    let [searchRender, setSearchRender] = useState();

    const searchFunction = async() =>{
        const response = await fetch('http://worldtimeapi.org/api/timezone/Etc/UTC');
        const data = await response.json();
        const currentDateTime = new Date(data.datetime);
        
        currentDateTime.setUTCHours(0, 0, 0, 0);
        const startOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        
        currentDateTime.setUTCHours(23, 59, 0 ,0);
        const endOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        fetch(url + "/search", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                text: searchInput.current.value,
                startOfDayFormatted: startOfDays,
                endOfDayFormatted: endOfDays,
                token: JSON.parse(localStorage.getItem("userInfo")).token
          })
        })
        .then(res => res.json())
        .then(data => {
            renderAllList.current.style.display = "none"
            renderWaitingList.current.style.display = "none"
            renderProcessList.current.style.display = "none"
            renderDoneList.current.style.display = "none"
            renderSearchList.current.style.display = "block"
            setSearchRender(data)
        })
    }

    const [isOpen, setIsOpen] = useState(false);
    const [isAddOpen, setIsAddOpen] = useState(false);

    const openModal = () => {
        setIsOpen(true)
    };

    const closeModal = () => {
        setIsOpen(false);
    };

    const handleModalClick = (e) => {
        if (e.target.classList.contains('modal')) {
        closeModal();
        }
    };

    const openAddModal = () => {
        setIsAddOpen(true)
    }

    const closeAddModal = () => {
        setIsAddOpen(false);
    };

    const handleAddModalClick = (e) => {
        if (e.target.classList.contains('addmodal')) {
            closeAddModal();
        }
    };


    const [selectedOption, setSelectedOption] = useState('');

    const handleSelectChange = (event) => {
        setSelectedOption(event.value);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
    };

    const { data: patients, loading } = useQuery(PATIENTS)

    let options = [];

    const [registerPatient] = useMutation(REGISTER_DAILY_PATIENTS, {
        update: (cache, data) => {
            console.log(data)
          alert("PATIENT REGISTERED!")
          window.location.reload(false);
        }
    })

    if (patients && patients.patients) {
        options = options.concat(
          patients.patients.map(patient => ({
            value: patient.id,
            label: patient.surname + " " + patient.name + " " + patient.lastname
          }))
        );
    }

    const registerClientToDay = e => {
        e.preventDefault()

        registerPatient({
            variables: {
              time: localDateTime,
              patient: selectedOption,
              isActive: true
            }
        })
    }

    const [updateDailyPatientsStatus] = useMutation(UPDATE_DAILY_PATIENTS_STATUS, {
        update: (cache, data) => {
          alert("PATIENT REGISTERED!")
          window.location.reload()
        }
    })

    const changeStatusOfPatients = (event) => { 
        updateDailyPatientsStatus({
            variables: {
              id: event.target.name,
              status: event.target.value
            }
        })
    }           

    const [addPatient] = useMutation(ADD_PATIENTS, {
        update: (cache, data) => {
          alert("PATIENT ADDED!");
          window.location.reload(false);
        },
        onError: (error) => {
          console.error('Mutation error:', error);
        }
      });
      

    const addPatientFully = e => {
        e.preventDefault()

        const { name, surname, lastname, phone, gender, home, age } = e.target.elements

        if(gender === "true"){
            addPatient({
                variables: {
                    name: name.value,
                    surname: surname.value,
                    lastname: lastname.value,
                    phone: phone.value,
                    gender: true,
                    home: home.value,
                    age: parseInt(age.value),
                    isActive: true
                }
            })
        } else {
            addPatient({
                variables: {
                    name: name.value,
                    surname: surname.value,
                    lastname: lastname.value,
                    phone: phone.value,
                    gender: false,
                    home: home.value,
                    age: parseInt(age.value),
                    isActive: true
                }
            })
        }
    }
    return (
        <div className="rendered-area patients-render" style={{ width: rightWidth, height: rightBottomHeight}}>
            <div className="patients-render-top" >
                <div className="patients-render-top-box">
                    <h1>{patientsTopWaiting}</h1>
                    <p>Kutish</p>
                </div>
                <div className="patients-render-top-box"
                    style={{borderLeft: "1px solid #C4C4C4", borderRight: "1px solid #C4C4C4"}}>
                    <h1>{patientsTopProcess}</h1>
                    <p>Jarayonda</p>
                </div>
                <div className="patients-render-top-box">
                    <h1>{patientsTopDone}</h1>
                    <p>Bajarildi</p>
                </div>
            </div>
            <div className="patients-render-bottom" style={{ width: rightWidth, height: rightBottomHeight}}>
                <div className="patients-render-bottom-top">
                    <div className="patients-render-bottom-top-box">
                        <input type="text" placeholder="Search" ref={searchInput} />
                        <button onClick={searchFunction}>Qidirmoq</button>
                        <button onClick={openAddModal}>Qo'shish</button>
                        <button onClick={openModal}>Royxatdan otish</button>
                    </div>
                    <div className="patients-render-bottom-top-row">
                        <button onClick={renderAllTrigger}
                            style={{backgroundColor: `${renderAllButton ? "#0832DE" : "#F6F6F6"}`, color: `${renderAllButton ? "white" : "#343434"}`}}>HAMMA</button>
                        <button onClick={renderWaitingTrigger}
                            style={{marginLeft: "15px", backgroundColor: `${renderWaitingButton ? "#0832DE" : "#F6F6F6"}`, color: `${renderWaitingButton ? "white" : "#343434"}`}}>KUTISH</button>
                        <button onClick={renderProcessTrigger}
                            style={{marginLeft: "15px", backgroundColor: `${renderProcessButton ? "#0832DE" : "#F6F6F6"}`, color: `${renderProcessButton ? "white" : "#343434"}`}}>JARAYONDA</button>
                        <button onClick={renderDoneTrigger}
                            style={{marginLeft: "15px", backgroundColor: `${renderDoneButton ? "#0832DE" : "#F6F6F6"}`, color: `${renderDoneButton ? "white" : "#343434"}`}}>BAJARILDI</button>
                    </div>
                </div>
                <div className="patients-render-bottom-render">
                    <div className="patients-render-bottom-render-example">
                        <h1 style={{width: "40%"}}>F.I.O</h1>
                        <h1 style={{width: "25%"}}>Telefon</h1>
                        <h1 style={{width: "15%"}}>Vaqt</h1>
                        <h1 style={{width: "10%"}}>Harakat</h1>
                    </div>
                    <div className="patients-render-bottom-render-list" style={{height: renderBottomBottomPatients, border: "10px solid black"}}
                        ref={renderSearchList}>
                        {
                        searchRender && searchRender.map((e, i) => (
                        <div value={e.id} key={i} className="patients-render-bottom-render-box">
                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.surname} {e.name} {e.lastname}</h1>
                            <h1 style={{width: "25%"}}>{e.phone}</h1>
                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                            <select onChange={changeStatusOfPatients} style={{width: "10%", marginRight: "5%"}} name={e.id}
                                defaultValue={`${e.status}`}>
                                <option value="WAITING">Waiting</option>
                                <option value="PROCESS">In Process</option>
                                <option value="DONE">Done</option>
                            </select>
                        </div>
                        ))
                        }
                    </div>
                    <div className="patients-render-bottom-render-list"
                        style={{height: renderBottomBottomPatients, display: `${renderAllButton ? "block" : "none"}`}}
                        ref={renderAllList}>
                        {
                        patientsRender && patientsRender.map((e, i) => (
                        <div value={e.id} key={i} className="patients-render-bottom-render-box">
                            <h1 style={{width: "40%"}}>{e.surname} {e.name} {e.lastname}</h1>
                            <h1 style={{width: "25%"}}>{e.phone}</h1>
                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                            <select onChange={changeStatusOfPatients} style={{width: "10%", marginRight: "5%"}} name={e.id}
                                defaultValue={`${e.status}`}>
                                <option value="WAITING">Waiting</option>
                                <option value="PROCESS">In Process</option>
                                <option value="DONE">Done</option>
                            </select>
                        </div>
                        ))
                        }
                    </div>
                    <div className="patients-render-bottom-render-list"
                        style={{height: renderBottomBottomPatients, display: `${renderWaitingButton ? "block" : "none"}`}}
                        ref={renderWaitingList}>
                        {
                        patientsRender && patientsRender.map((e, i) => (
                        <div value={e.id} key={i} className="patients-render-bottom-render-box"
                            style={{display: `${e.status === "WAITING" ? "flex" : "none" }`}}>
                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.surname} {e.name} {e.lastname}</h1>
                            <h1 style={{width: "25%"}}>{e.phone}</h1>
                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                            <select onChange={changeStatusOfPatients} style={{width: "10%", marginRight: "5%"}} name={e.id}
                                defaultValue={`${e.status}`}>
                                <option value="WAITING">Waiting</option>
                                <option value="PROCESS">In Process</option>
                                <option value="DONE">Done</option>
                            </select>
                        </div>
                        ))
                        }
                    </div>
                    <div className="patients-render-bottom-render-list"
                        style={{height: renderBottomBottomPatients, display: `${renderProcessButton ? "block" : "none"}`}}
                        ref={renderProcessList}>
                        {
                        patientsRender && patientsRender.map((e, i) => (
                        <div value={e.id} key={i} className="patients-render-bottom-render-box"
                            style={{display: `${e.status === "PROCESS" ? "flex" : "none" }`}}>
                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.surname} {e.name} {e.lastname}</h1>
                            <h1 style={{width: "25%"}}>{e.phone}</h1>
                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                            <select onChange={changeStatusOfPatients} style={{width: "10%", marginRight: "5%"}} name={e.id}
                                defaultValue={`${e.status}`}>
                                <option value="WAITING">Waiting</option>
                                <option value="PROCESS">In Process</option>
                                <option value="DONE">Done</option>
                            </select>
                        </div>
                        ))
                        }
                    </div>
                    <div className="patients-render-bottom-render-list"
                        style={{height: renderBottomBottomPatients, display: `${renderDoneButton ? "block" : "none"}`}}
                        ref={renderDoneList}>
                        {
                        patientsRender && patientsRender.map((e, i) => (
                        <div value={e.id} key={i} className="patients-render-bottom-render-box"
                            style={{display: `${e.status === "DONE" ? "flex" : "none" }`}}>
                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.surname} {e.name} {e.lastname}</h1>
                            <h1 style={{width: "25%"}}>{e.phone}</h1>
                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                            <select style={{width: "10%", marginRight: "5%"}} defaultValue={`DONE`}>
                                <option value="DONE">Done</option>
                            </select>
                        </div>
                        ))
                        }
                    </div>
                </div>
            </div>
            <div className={`modal ${isOpen ? 'open' : '' }`} onClick={handleModalClick}>
                <form className="modal-content" onSubmit={handleSubmit}>
                    <h2>Registration</h2>
                    <Select value={selectedOption} onChange={handleSelectChange} options={options} isSearchable
                        placeholder="Search patients" />
                    <button onClick={registerClientToDay}>Submit</button>
                    <button style={{backgroundColor: "#FB4B4B", marginLeft: "10px"}} onClick={closeModal}>Close</button>
                </form>
            </div>
            <div className={`addmodal ${isAddOpen ? 'open' : '' }`} onClick={handleAddModalClick}>
                <form className="addmodal-content" onSubmit={addPatientFully}>
                    <h2>Add New Patient</h2>
                    <input type="text" name="name" placeholder="Name" autoComplete='off'/>
                    <input type="text" name="surname" placeholder="Surname" autoComplete='off'/>
                    <input type="text" name="lastname" placeholder="Lastname" autoComplete='off'/>
                    <input type="text" name="phone" placeholder="Phone-number" autoComplete='off'/>
                    <input type="text" name="home" placeholder="Home" autoComplete='off'/>
                    <input type="number" name="age" placeholder="Age" autoComplete='off'/>
                    <div style={{"display": "flex"}}>
                    <label className="label radio-label" style={{"display": "flex"}}>
                        <input type="radio" name="gender" value="true" />
                        <p>Male</p>
                    </label>
                    <label className="label radio-label" style={{"display": "flex"}}>
                        <input type="radio" name="gender"  value="false"  />
                        <p>Female</p>
                    </label>
                    </div>
                    <button className="submit-add-patient" type="submit">Submit</button>
                    <button className="close-add-patient" onClick={closeAddModal}>Close</button>
                </form>
            </div>
        </div>
    );
}

export default Patients;