const CircleChart = ({ percentages }) => {
    const canvasRef = useRef(null);
  
    function calculateAngles(percentages) {
      const total = percentages.reduce((sum, percent) => sum + percent, 0);
      const angles = percentages.map((percent) => (percent / total) * 360);
      return angles;
    }
  
    function drawCircleChart(context, percentages) {
      const centerX = context.canvas.width / 2;
      const centerY = context.canvas.height / 2;
      const radius = Math.min(centerX, centerY) - 10;
      const angles = calculateAngles(percentages);
  
      let startAngle = 0;
  
      for (let i = 0; i < percentages.length; i++) {
        const endAngle = startAngle + (angles[i] * Math.PI) / 180;
        context.beginPath();
        context.moveTo(centerX, centerY);
        context.arc(centerX, centerY, radius, startAngle, endAngle, false);
        context.closePath();
  
        // Choose a different color for each segment
        const colors = ['#4318FF', '#6AD2FF', '#EFF4FB'];
        context.fillStyle = colors[i];
        context.fill();
  
        startAngle = endAngle;
      }
    }
  
    useEffect(() => {
      const canvas = canvasRef.current;
      const context = canvas.getContext('2d');
      drawCircleChart(context, percentages);
    }, [percentages]);
  
    return <canvas ref={canvasRef} width={200} height={200} />;
  };
