import { gql } from '@apollo/client'

const PATIENTS = gql`
query {
  patients {
      id
      name
      surname
      lastname
      phone
      gender
      home
      age 
  }
}  
`

const REGISTER_DAILY_PATIENTS = gql`
mutation newDailyPatients($time: String! $patient: ID! $isActive: Boolean!){
  newDailyPatients(time: $time patient: $patient isActive: $isActive)
}
`

const UPDATE_DAILY_PATIENTS_STATUS = gql`
mutation updateDailyPatientsStatus($id: ID! $status: String!){
  updateDailyPatientsStatus(id: $id status: $status)
}
`

const UPDATE_DAILY_SERVICE_STATUS = gql`
mutation updateDailyServiceStatus($id: ID! $status: String!){
  updateDailyServiceStatus(id: $id status: $status)
}
`

const REGISTER_DAILY_SERVICE = gql`
mutation newDailyServices($time: String! $service: ID! $staff: ID! $patient: ID! $isActive: Boolean!){
  newDailyServices(time: $time service: $service staff: $staff patient: $patient isActive: $isActive)
}
`

const SERVICE = gql`
query {
  services {
      id
      name
      price
  }
}  
`

const STAFF = gql`
query {
  staff {
      id
      name
      secondname
  }
}  
`
const ADD_PATIENTS = gql`
  mutation newPatients(
    $name: String!,
    $surname: String!,
    $lastname: String!,
    $phone: String!,
    $gender: Boolean!,
    $home: String!,
    $age: Int!,
    $isActive: Boolean!
  ) {
    newPatients(
      name: $name,
      surname: $surname,
      lastname: $lastname,
      phone: $phone,
      gender: $gender,
      home: $home,
      age: $age,
      isActive: $isActive
    )
  }
`;

export {
  PATIENTS,
  REGISTER_DAILY_PATIENTS,
  UPDATE_DAILY_PATIENTS_STATUS,
  UPDATE_DAILY_SERVICE_STATUS,
  REGISTER_DAILY_SERVICE,
  SERVICE,
  STAFF,
  ADD_PATIENTS
}