import "../CSS/Services.css"

import { useEffect, useRef, useState } from "react";
import url from "../../../config.js"
import React from 'react';
import Select from 'react-select';

import { useQuery, useMutation } from '@apollo/client'
import { UPDATE_DAILY_SERVICE_STATUS, REGISTER_DAILY_SERVICE, SERVICE, STAFF, PATIENTS } from '../../../Query'

function Service() {
    //User Info Checker Starts

    // const [actualInfo, setActualInfo] = useState(0);

    const handleGetUserActualInfo = (token) => {
        
    }

    const handleCheckUserInfo = () => {
        const userInfo = JSON.parse(localStorage.getItem('userInfo'));
        if(userInfo != null){
            if(userInfo.status == 'SUPER_ADMIN'){
                window.location.href = "/superadmin"
            }else if(userInfo.status == 'ADMIN'){
                handleGetUserActualInfo(userInfo.token)
            }else if(userInfo.status == 'DOCTOR'){
                window.location.href = "/doctor"
            }else {
                window.location.href = "/notfound"
            }
        }else {
            window.location.href = "/login"
        }
    }
    handleCheckUserInfo()

    const [localDateTime, setLocalDateTime] = useState('');

    useEffect(() => {
        fetch('https://worldtimeapi.org/api/ip')
        .then(response => response.json())
        .then(data => {
            const datetime = data.datetime.substring(0, 23) + 'Z';
            setLocalDateTime(datetime);
        })
        .catch(error => {
            console.error('Error fetching local date and time:', error);
        });
    }, []);

    //User Info Checker Ends

    let [serviceTopWaiting, setServiceTopWaiting] = useState(0);
    let [serviceTopProcess, setServiceTopProcess] = useState(0);
    let [serviceTopDone, setServiceTopDone] = useState(0);
    let [serviceTopRender, setServiceTopRender] = useState(null);

    useEffect(() => {
        // const localStorageValue = localStorage.getItem('status');
        // if(localStorageValue == "service") {
        const getService = async() => {
            const response = await fetch('http://worldtimeapi.org/api/timezone/Etc/UTC');
                    const data = await response.json();
                    const currentDateTime = new Date(data.datetime);
        
                    currentDateTime.setUTCHours(0, 0, 0, 0);
                    const startOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        
                    currentDateTime.setUTCHours(23, 59, 0 ,0);
                    const endOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        fetch(url + "/get_monthly_service", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                startOfDayFormatted: startOfDays,
                endOfDayFormatted: endOfDays,
                token: JSON.parse(localStorage.getItem("userInfo")).token
          })
        })
        .then(res => res.json())
        .then(data => {
            setServiceTopRender(data)
            let Waiting = 0;
                let Process = 0;
                let Done = 0;
                for(let i = 0; i < data.length; i++){
                    if(data[i].status == "WAITING") {
                        Waiting++
                    } else if(data[i].status == "PROCESS") {
                        Process++
                    } else if(data[i].status == "DONE") {
                        Done++
                    } else {
                        console.log("Something went wrong!")
                    }
                }

                setServiceTopWaiting(Waiting)
                setServiceTopProcess(Process)
                setServiceTopDone(Done)
        })
    }
    getService()
        // }
    }, [])


    let [serviceAllButton, setServiceAllButton] = useState(true);
    let [serviceWaitingButton, setServiceWaitingButton] = useState(false);
    let [serviceProcessButton, setServiceProcessButton] = useState(false);
    let [serviceDoneButton, setServiceDoneButton] = useState(false);

    function serviceAllTrigger(){
        renderSearchServiceList.current.style.display = "none"
        setServiceAllButton(true)
        setServiceWaitingButton(false)
        setServiceProcessButton(false)
        setServiceDoneButton(false)
    }

    function serviceWaitingTrigger(){
        renderSearchServiceList.current.style.display = "none"
        setServiceAllButton(false)
        setServiceWaitingButton(true)
        setServiceProcessButton(false)
        setServiceDoneButton(false)
    }

    function serviceProcessTrigger(){
        renderSearchServiceList.current.style.display = "none"
        setServiceAllButton(false)
        setServiceWaitingButton(false)
        setServiceProcessButton(true)
        setServiceDoneButton(false)
    }

    function serviceDoneTrigger(){
        renderSearchServiceList.current.style.display = "none"
        setServiceAllButton(false)
        setServiceWaitingButton(false)
        setServiceProcessButton(false)
        setServiceDoneButton(true)
    }

    const [updateDailyServiceStatus] = useMutation(UPDATE_DAILY_SERVICE_STATUS, {
        update: (cache, data) => {
          alert("Service updated!")
          window.location.reload()
        }
    })

    const changeStatusService = (event) =>{
        updateDailyServiceStatus({
            variables: {
              id: event.target.name,
              status: event.target.value
            }
        })
    }

    let renderSearchServiceList = useRef();
    let [searchServiceList, setSearchServiceList] = useState();
    let searchServiceInput = useRef();

    const searchServiceFunction = async() => {
        const response = await fetch('http://worldtimeapi.org/api/timezone/Etc/UTC');
                    const data = await response.json();
                    const currentDateTime = new Date(data.datetime);
        
                    currentDateTime.setUTCHours(0, 0, 0, 0);
                    const startOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        
                    currentDateTime.setUTCHours(23, 59, 0 ,0);
                    const endOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        fetch(url + "/search_service", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                text: searchServiceInput.current.value,
                startOfDayFormatted: startOfDays,
                endOfDayFormatted: endOfDays,
                token: JSON.parse(localStorage.getItem("userInfo")).token
          })
        })
        .then(res => res.json())
        .then(data => {
            setServiceAllButton(false)
            setServiceWaitingButton(false)
            setServiceProcessButton(false)
            setServiceDoneButton(false)
            renderSearchServiceList.current.style.display = "block"
            setSearchServiceList(data)
        })
    }

    const [isSOpen, setIsSOpen] = useState(false);

    function openServiceModal(){
        setIsSOpen(true);
    }

    const closeServiceModal = () => {
        setIsSOpen(false);
    };

    const handleServiceModalClick = (e) => {
        if (e.target.classList.contains('modalService')) {
            closeServiceModal();
        }
    };

    const [selectedServiceOption, setSelectedServiceOption] = useState('');
    const [selectedServicePatientOption, setSelectedServicePatientOption] = useState('');
    const [selectedServiceStaffOption, setSelectedServiceStaffOption] = useState('');

    const handleServiceSelectChange = (event) => {
        setSelectedServiceOption(event.value);
    };

    const handlePatientSelectChange = (event) => {
        setSelectedServicePatientOption(event.value);
    };

    const handleStaffSelectChange = (event) => {
        setSelectedServiceStaffOption(event.value);
    };

    const { data: serviceS } = useQuery(SERVICE)
    const { data: patientS } = useQuery(PATIENTS)
    const { data: staffS } = useQuery(STAFF)

    let optionsServiceS = [];
    let optionsPatientS = [];
    let optionsStaffS = [];

    if (serviceS && serviceS.services) {
        optionsServiceS = optionsServiceS.concat(
            serviceS.services.map(servicew => ({
                value: servicew.id,
                label: servicew.name + " " + servicew.price + 'uzs'
            }))
        );
    }

    if (staffS && staffS.staff) {
        optionsStaffS = optionsStaffS.concat(
            staffS.staff.map(servicew => ({
                value: servicew.id,
                label: servicew.secondname + " " + servicew.name
            }))
        );
    }

    if (patientS && patientS.patients) {
        optionsPatientS = optionsPatientS.concat(
            patientS.patients.map(servicew => ({
                value: servicew.id,
                label: servicew.surname + " " + servicew.name + " " + servicew.lastname
            }))
        );
    }

    const registerClientServiceToDay = e => {
        e.preventDefault()

        registerService({
            variables: {
              time: localDateTime,
              service: selectedServiceOption,
              staff: selectedServiceStaffOption,
              patient: selectedServicePatientOption,
              isActive: true
            }
        })
    }

    const [registerService] = useMutation(REGISTER_DAILY_SERVICE, {
        update: (cache, data) => {
          alert("SERVICE CREATED!")
          window.location.reload(false);
        }   
    })

    //Navbar System Ends

    //////////////////////////////////////////////////////////////////////////////////////////////

    //Right Size Manager Starts

    const [rightWidth, setRightWidth] = useState(window.innerWidth - 255);

    useEffect(() => {
        const handleResize = () => {
            setRightWidth(window.innerWidth - 255);
        };

        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightHeight, setRightHeight] = useState(window.innerHeight - 100);

    useEffect(() => {
        const handleResize = () => {
            setRightHeight(window.innerHeight - 100);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightBottomHeight, setRightBottomHeight] = useState(window.innerHeight - 165);

    useEffect(() => {
        const handleResize = () => {
            setRightBottomHeight(window.innerHeight - 165);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [renderBottomBottomPatients, setRenderBottomBottomPatients] = useState(window.innerHeight - 340);

    useEffect(() => {
        const handleResize = () => {
            setRenderBottomBottomPatients(window.innerHeight - 340);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    //Right Size Manager Ends

    //////////////////////////////////////////////////////////////////////////////////////////////

    //Get current day and get start, end
    return (
       <div className="service-area service-render" style={{ width: rightWidth, height: rightHeight}}>
                    <div className="service-render-top">
                        <div className="service-render-top-box">
                            <h1 style={{color: "#FB4B4B"}}>{serviceTopWaiting}</h1>
                            <p style={{color: "#FB4B4B"}}>KUTISH</p>
                        </div>
                        <div className="service-render-top-box" style={{borderLeft: "1px solid #C4C4C4", borderRight: "1px solid #C4C4C4"}}>
                            <h1>{serviceTopProcess}</h1>
                            <p>JARAYONIDA</p>
                        </div>
                        <div className="service-render-top-box">
                            <h1 style={{color: "#13C39C"}}>{serviceTopDone}</h1>
                            <p style={{color: "#13C39C"}}>BAJARILDI</p>
                        </div>
                    </div>
                    <div className="service-render-bottom" style={{ width: rightWidth, height: rightBottomHeight}}>
                    <div className="service-render-bottom-top">
                            <div className="service-render-bottom-top-box">
                                <input type="text" placeholder="Search" ref={searchServiceInput}/>
                                <button onClick={searchServiceFunction}>Qidirmoq</button>
                                <button onClick={openServiceModal}>Yangi yaratish</button>
                            </div>
                            <div className="service-render-bottom-top-row">
                                <button onClick={serviceAllTrigger} style={{backgroundColor: `${serviceAllButton ? "#0832DE" : "#F6F6F6"}`, color: `${serviceAllButton ? "white" : "#343434"}`}}>HAMMA</button>
                                <button onClick={serviceWaitingTrigger} style={{marginLeft: "15px", backgroundColor: `${serviceWaitingButton ? "#0832DE" : "#F6F6F6"}`, color: `${serviceWaitingButton ? "white" : "#343434"}`}}>KUTISH</button>
                                <button onClick={serviceProcessTrigger} style={{marginLeft: "15px", backgroundColor: `${serviceProcessButton ? "#0832DE" : "#F6F6F6"}`, color: `${serviceProcessButton ? "white" : "#343434"}`}}>JARAYON</button>
                                <button onClick={serviceDoneTrigger} style={{marginLeft: "15px", backgroundColor: `${serviceDoneButton ? "#0832DE" : "#F6F6F6"}`, color: `${serviceDoneButton ? "white" : "#343434"}`}}>BAJARILDI</button>
                            </div>
                        </div>
                        <div className="service-render-bottom-render">
                            <div className="service-render-bottom-render-example">
                                <h1 style={{width: "40%", marginLeft: "5%"}}>Bemor</h1>
                                <h1 style={{width: "15%"}}>Telefon</h1>
                                <h1 style={{width: "10%"}}>Vaqt</h1>
                                <h1 style={{width: "10%", marginRight: "5%"}}>Harakat</h1>
                            </div>
                            <div className="service-render-bottom-render-list" style={{height: renderBottomBottomPatients}} ref={renderSearchServiceList}>
                            {
                                searchServiceList && searchServiceList.map((e, i) => (
                                    <div value={e.id} key={i} className="service-render-bottom-render-box">
                                        <div className="service-render-bottom-render-box-top">
                                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.patient_surname} {e.patient_name} {e.patient_lastname}</h1>
                                            <h1 style={{width: "15%"}}>{e.patient_phone}</h1>
                                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                                            <select onChange={changeStatusService} style={{width: "10%", marginRight: "5%"}} name={e.id} defaultValue={`${e.status}`}>
                                                <option value="WAITING">Waiting</option>
                                                <option value="PROCESS">In Process</option>
                                                <option value="DONE">Done</option>
                                            </select>
                                        </div>
                                        <div className="service-render-bottom-render-box-bottom">
                                            <h1 style={{width: "28%", marginLeft: "5%"}}>{e.service_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "1%"}}>
                                                {e.service_price.toString().length == 4 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 5 ? e.service_price.toString().substring(0,2) + "." + e.service_price.toString().substring(2,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 6 ? e.service_price.toString().substring(0,3) + "." + e.service_price.toString().substring(3,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 7 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,4) + "." + e.service_price.toString().substring(4,e.service_price.toString().length) : ""}
                                                 uzs</h1>
                                            <h1 style={{width: "10%", marginLeft: "10%"}}>{e.staff_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "2%"}}>{e.staff_status}</h1>
                                            <h1 style={{width: "20%", marginLeft: "2%",marginRight: "5%"}}>{e.staff_phone}</h1>
                                        </div>
                                    </div>
                                ))
                            }   
                            </div>
                            <div className="service-render-bottom-render-list" style={{height: renderBottomBottomPatients, display: `${serviceAllButton ? "block" : "none"}`}}>
                            {
                                serviceTopRender && serviceTopRender.map((e, i) => (
                                    <div value={e.id} key={i} className="service-render-bottom-render-box">
                                        <div className="service-render-bottom-render-box-top">
                                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.patient_surname} {e.patient_name} {e.patient_lastname}</h1>
                                            <h1 style={{width: "15%"}}>{e.patient_phone}</h1>
                                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                                            <select onChange={changeStatusService} style={{width: "10%", marginRight: "5%"}} name={e.id} defaultValue={`${e.status}`}>
                                                <option value="WAITING">Waiting</option>
                                                <option value="PROCESS">In Process</option>
                                                <option value="DONE">Done</option>
                                            </select>
                                        </div>
                                        <div className="service-render-bottom-render-box-bottom">
                                            <h1 style={{width: "28%", marginLeft: "5%"}}>{e.service_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "1%"}}>
                                                {e.service_price.toString().length == 4 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 5 ? e.service_price.toString().substring(0,2) + "." + e.service_price.toString().substring(2,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 6 ? e.service_price.toString().substring(0,3) + "." + e.service_price.toString().substring(3,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 7 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,4) + "." + e.service_price.toString().substring(4,e.service_price.toString().length) : ""}
                                                 uzs</h1>
                                            <h1 style={{width: "10%", marginLeft: "10%"}}>{e.staff_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "2%"}}>{e.staff_status}</h1>
                                            <h1 style={{width: "20%", marginLeft: "2%",marginRight: "5%"}}>{e.staff_phone}</h1>
                                        </div>
                                    </div>
                                ))
                            }   
                            </div>
                            <div className="service-render-bottom-render-list" style={{height: renderBottomBottomPatients, display: `${serviceWaitingButton ? "block" : "none"}`}}>
                            {
                                serviceTopRender && serviceTopRender.map((e, i) => (
                                    <div value={e.id} key={i} className="service-render-bottom-render-box" style={{display: `${e.status === "WAITING" ? "block" : "none" }`}}>
                                        <div className="service-render-bottom-render-box-top">
                                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.patient_surname} {e.patient_name} {e.patient_lastname}</h1>
                                            <h1 style={{width: "15%"}}>{e.patient_phone}</h1>
                                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                                            <select onChange={changeStatusService} style={{width: "10%", marginRight: "5%"}} name={e.id} defaultValue={`${e.status}`}>
                                                <option value="WAITING">Waiting</option>
                                                <option value="PROCESS">In Process</option>
                                                <option value="DONE">Done</option>
                                            </select>
                                        </div>
                                        <div className="service-render-bottom-render-box-bottom">
                                            <h1 style={{width: "28%", marginLeft: "5%"}}>{e.service_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "1%"}}>
                                                {e.service_price.toString().length == 4 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 5 ? e.service_price.toString().substring(0,2) + "." + e.service_price.toString().substring(2,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 6 ? e.service_price.toString().substring(0,3) + "." + e.service_price.toString().substring(3,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 7 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,4) + "." + e.service_price.toString().substring(4,e.service_price.toString().length) : ""}
                                                 uzs</h1>
                                            <h1 style={{width: "10%", marginLeft: "10%"}}>{e.staff_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "2%"}}>{e.staff_status}</h1>
                                            <h1 style={{width: "20%", marginLeft: "2%",marginRight: "5%"}}>{e.staff_phone}</h1>
                                        </div>
                                    </div>
                                ))
                            }   
                            </div>
                            <div className="service-render-bottom-render-list" style={{height: renderBottomBottomPatients, display: `${serviceProcessButton ? "block" : "none"}`}}>
                            {
                                serviceTopRender && serviceTopRender.map((e, i) => (
                                    <div value={e.id} key={i} className="service-render-bottom-render-box" style={{display: `${e.status === "PROCESS" ? "block" : "none" }`}}>
                                        <div className="service-render-bottom-render-box-top">
                                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.patient_surname} {e.patient_name} {e.patient_lastname}</h1>
                                            <h1 style={{width: "15%"}}>{e.patient_phone}</h1>
                                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                                            <select onChange={changeStatusService} style={{width: "10%", marginRight: "5%"}} name={e.id} defaultValue={`${e.status}`}>
                                                <option value="WAITING">Waiting</option>
                                                <option value="PROCESS">In Process</option>
                                                <option value="DONE">Done</option>
                                            </select>
                                        </div>
                                        <div className="service-render-bottom-render-box-bottom">
                                            <h1 style={{width: "28%", marginLeft: "5%"}}>{e.service_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "1%"}}>
                                                {e.service_price.toString().length == 4 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 5 ? e.service_price.toString().substring(0,2) + "." + e.service_price.toString().substring(2,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 6 ? e.service_price.toString().substring(0,3) + "." + e.service_price.toString().substring(3,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 7 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,4) + "." + e.service_price.toString().substring(4,e.service_price.toString().length) : ""}
                                                 uzs</h1>
                                            <h1 style={{width: "10%", marginLeft: "10%"}}>{e.staff_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "2%"}}>{e.staff_status}</h1>
                                            <h1 style={{width: "20%", marginLeft: "2%",marginRight: "5%"}}>{e.staff_phone}</h1>
                                        </div>
                                    </div>
                                ))
                            }   
                            </div>
                            <div className="service-render-bottom-render-list" style={{height: renderBottomBottomPatients, display: `${serviceDoneButton ? "block" : "none"}`}}>
                            {
                                serviceTopRender && serviceTopRender.map((e, i) => (
                                    <div value={e.id} key={i} className="service-render-bottom-render-box" style={{display: `${e.status === "DONE" ? "block" : "none" }`}}>
                                        <div className="service-render-bottom-render-box-top">
                                            <h1 style={{width: "40%", marginLeft: "5%"}}>{e.patient_surname} {e.patient_name} {e.patient_lastname}</h1>
                                            <h1 style={{width: "15%"}}>{e.patient_phone}</h1>
                                            <h1 style={{width: "10%"}}>{e.time.substring(11,16)}</h1>
                                            <select onChange={changeStatusService} style={{width: "10%", marginRight: "5%"}} name={e.id} defaultValue={`${e.status}`}>
                                                <option value="WAITING">Waiting</option>
                                                <option value="PROCESS">In Process</option>
                                                <option value="DONE">Done</option>
                                            </select>
                                        </div>
                                        <div className="service-render-bottom-render-box-bottom">
                                            <h1 style={{width: "28%", marginLeft: "5%"}}>{e.service_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "1%"}}>
                                                {e.service_price.toString().length == 4 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 5 ? e.service_price.toString().substring(0,2) + "." + e.service_price.toString().substring(2,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 6 ? e.service_price.toString().substring(0,3) + "." + e.service_price.toString().substring(3,e.service_price.toString().length) : ""}
                                                {e.service_price.toString().length == 7 ? e.service_price.toString().substring(0,1) + "." + e.service_price.toString().substring(1,4) + "." + e.service_price.toString().substring(4,e.service_price.toString().length) : ""}
                                                 uzs</h1>
                                            <h1 style={{width: "10%", marginLeft: "10%"}}>{e.staff_name}</h1>
                                            <h1 style={{width: "10%", marginLeft: "2%"}}>{e.staff_status}</h1>
                                            <h1 style={{width: "20%", marginLeft: "2%",marginRight: "5%"}}>{e.staff_phone}</h1>
                                        </div>
                                    </div>
                                ))
                            }   
                            </div>
                        </div>
                        <div className={`modalService ${isSOpen ? 'sOpen' : ''}`} onClick={handleServiceModalClick}>
            <form className="modal-service-content">
                <h2>Registration</h2>
                <Select
                value={selectedServiceOption}
                onChange={handleServiceSelectChange}
                options={optionsServiceS}
                isSearchable
                placeholder="Search service"
                />
                <Select
                value={selectedServiceStaffOption}
                onChange={handleStaffSelectChange}
                options={optionsStaffS}
                isSearchable
                placeholder="Search staff"
                className="selectinservicemodal"
                />
                <Select
                value={selectedServicePatientOption}
                onChange={handlePatientSelectChange}
                options={optionsPatientS}
                isSearchable
                placeholder="Search patients"
                className="selectinservicemodal"
                />
                <button onClick={registerClientServiceToDay}>Submit</button>
                <button style={{backgroundColor: "#FB4B4B", marginLeft: "10px"}} onClick={closeServiceModal}>Close</button>
            </form>
        </div>
                    </div>
       </div>
    );
}

export default Service;
  