import "../CSS/Statistcs.css"

import Heading from "../../../Images/Heading.png"
import Payment from "../../../Images/payment.png"
import { useEffect, useRef, useState } from "react";
import url from "../../../config.js"
import React from 'react';

function Statistics() {
    //User Info Checker Starts

    // const [actualInfo, setActualInfo] = useState(0);

    const handleGetUserActualInfo = (token) => {
        
    }

    const handleCheckUserInfo = () => {
        const userInfo = JSON.parse(localStorage.getItem('userInfo'));
        if(userInfo != null){
            if(userInfo.status == 'SUPER_ADMIN'){
                window.location.href = "/superadmin"
            }else if(userInfo.status == 'ADMIN'){
                handleGetUserActualInfo(userInfo.token)
            }else if(userInfo.status == 'DOCTOR'){
                window.location.href = "/doctor"
            }else {
                window.location.href = "/notfound"
            }
        }else {
            window.location.href = "/login"
        }
    }
    handleCheckUserInfo()

    //User Info Checker Ends

    //////////////////////////////////////////////////////////////////////////////////////////////

    const [rightWidth, setRightWidth] = useState(window.innerWidth - 255);

    useEffect(() => {
        const handleResize = () => {
            setRightWidth(window.innerWidth - 255);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const [rightHeight, setRightHeight] = useState(window.innerHeight - 100);

    useEffect(() => {
        const handleResize = () => {
            setRightHeight(window.innerHeight - 100);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    //Right Size Manager Ends

    //////////////////////////////////////////////////////////////////////////////////////////////

    //Get current day and get start, end

    let [patientsAllCount, setPatientsAllCount] = useState(0);
    let [dailyMoney, setDailyMoney] = useState(0);
    let [dailyDate, setDate] = useState(0);

    let [patientStatW, setPatientStatW] = useState(0);
    let [patientStatP, setPatientStatP] = useState(0);
    let [patientStatD, setPatientStatD] = useState(0);

    let [countStatW, setCountStatW] = useState(0);
    let [countStatP, setCountStatP] = useState(0);
    let [countStatD, setCountStatD] = useState(0);

    useEffect(() => {
        // const localStorageValue = localStorage.getItem('status');
        // if(localStorageValue == "statistics") {
        const getMoney = async() => {
            const response = await fetch('http://worldtimeapi.org/api/timezone/Etc/UTC');
                    const data = await response.json();
                    const currentDateTime = new Date(data.datetime);
        
                    currentDateTime.setUTCHours(0, 0, 0, 0);
                    const startOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        
                    currentDateTime.setUTCHours(23, 59, 0 ,0);
                    const endOfDays = currentDateTime.toISOString().slice(0, 23).replace('T', ' ');
        fetch(url + "/get_monthly_patients", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                startOfDayFormatted: startOfDays,
                endOfDayFormatted: endOfDays,
                token: JSON.parse(localStorage.getItem("userInfo")).token
          })
        })
        .then(res => res.json())
        .then(data => {
            let count = 0;
            for(let i = 0; i < data.length; i++){
                setPatientsAllCount(++count)
            }

            let Waiting = 0;
            let Process = 0;
            let Done = 0;
            for(let i = 0; i < data.length; i++){
                if(data[i].status == "WAITING") {
                    Waiting++
                } else if(data[i].status == "PROCESS") {
                    Process++
                } else if(data[i].status == "DONE") {
                    Done++
                } else {
                    console.log("Something went wrong!")
                }
            }

            setCountStatW(Waiting)
            setCountStatP(Process)
            setCountStatD(Done)

            let countAll = Waiting + Process + Done;
            setPatientStatW(Math.round((Waiting / countAll) * 100))
            setPatientStatP(Math.round((Process / countAll) * 100))
            setPatientStatD(Math.round((Done / countAll) * 100))
        })
        fetch(url + "/get_monthly_money", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                startOfDayFormatted: startOfDays,
                endOfDayFormatted: endOfDays,
                token: JSON.parse(localStorage.getItem("userInfo")).token
          })
        })
        .then(res => res.json())
        .then(data => {
            if(data[0].overall){
                let money = data[0].overall.toString()
                let moneyResult = ""
                if(money.length == 4){
                    moneyResult = money.substring(0,1) + "." + money.substring(1, money.length);
                }else if(money.length == 5){
                    moneyResult = money.substring(0,2) + "." + money.substring(2, money.length);
                }else if(money.length == 6){
                    moneyResult = money.substring(0,3) + "." + money.substring(3, money.length);
                }else if(money.length == 7){
                    moneyResult = money.substring(0,1) + "." + money.substring(1, 4) + "." + money.substring(4, money.length);
                }else {
                    moneyResult = money
                }
                setDailyMoney(moneyResult)
                setDate(data[0].from.substring(0,10))
            }
            else {
                console.log("Offline")
            }
        })
    }
    getMoney()
// }
    }, [])

    return (
        <div className="rendered-area statistics-render" style={{width: rightWidth, height: rightHeight}}>
        Statistiks
        {/* <div className="statistics-top">
            <div className="statistics-top-top">
                <h1>Tafsilotlar</h1>
            </div>
            <div className="statistics-top-bottom">
                <div className="statistics-top-bottom-left">
                    <img src={Payment} alt="img" />
                    <h1>{dailyMoney}som</h1>
                    <h3>{dailyDate}</h3>
                    <p>Faqat bugungi kun uchun daromad, administrator har doim daromadni ko'raolmaydi</p>
                </div>
                <div className="statistics-top-bottom-right">
                    <img src={Heading} alt="img" />
                    <h1>{patientsAllCount} bemorlar</h1>
                    <p>Bugun biz qabul qilgan bemorlar soni</p>
                    <button>View</button>
                </div>
            </div>
        </div>
        <div className="statistics-bottom">
            <div className="statistics-bottom-top">
                <h1>Statistika</h1>
            </div>
            <div className="statistics-bottom-bottom">
                <div className="statistics-bottom-bottom-text">
                    <h1>{countStatD} <br /><span>BAJARILDI</span></h1>
                    <h1>{countStatP} <br /><span>JARAYONDA</span></h1>
                    <h1>{countStatW} <br /><span>KUTILMOQDA</span></h1>
                </div>
                <div className="statistics-bottom-bottom-diagram">
                    <div style={{width: `${patientStatD}%`, backgroundColor: "#4340DA", borderRadius: "8px 0px 0px 8px"}}>{patientStatD}%</div>
                    <div style={{width: `${patientStatP}%`, backgroundColor: "#CED6F8", color: "black"}}>{patientStatP}%</div>
                    <div style={{width: `${patientStatW}%`, backgroundColor: "#F5F4FF", color: "black", borderRadius: "0px 8px 8px 0px"}}>{patientStatW}%</div>
                </div>
            </div>
        </div> */}
    </div>
    );
}

export default Statistics;
  