import "./Login.css"
import url from "../../config.js"
import { useRef } from "react";
import { useNavigate } from 'react-router-dom';

function Login() {
  const navigate = useNavigate();
    let submitButton = useRef(null)
    const handleCheckUserInfo = () => {
        const userInfo = JSON.parse(localStorage.getItem('userInfo'));
        if(userInfo != null){
          navigate('/');
        }else {
          console.clear()
        }
      }
    handleCheckUserInfo()
    const login = e => {
        e.preventDefault()

    const { phone, password } = e.target.elements
    fetch(url + "/login", {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          userphone: phone.value,
          userpassword: password.value,
      })
    })
    .then(res => res.json())
    .then(data => {
      if(Array.isArray(data) && data.length > 0){
        const userInfo = {
          token: data[0].token,
          name: data[0].name,
          surname: data[0].surname,
          status: data[0].status, 
          branch: data[0].branch
        }
        localStorage.setItem("userInfo", JSON.stringify(userInfo));
        navigate('/');
      }else {
        submitButton.current.textContent = "Not found!"
        submitButton.current.className = "login-button-incorrect"
        setTimeout(() => {
            submitButton.current.textContent = "Submit"
            submitButton.current.className = "login-button"
        }, 2000)
      }
    })
    .catch(err => console.log(err))
    }
    return (
        <div className="container">
         <div className="top"></div>
         <div className="bottom"></div>
         <form className="center" onSubmit={login}>
           <h2 style={{"marginBottom": "40px"}}>Please Sign In</h2>
           <input name="phone" type="text" placeholder="Phone" autoComplete="off" required/>
           <input name="password" type="password" placeholder="Password" autoComplete="off" required/>
           <h2>&nbsp;</h2>
           <button type="submit" ref={submitButton} className="login-button">Submit</button>
         </form>
      </div>
    );
}

export default Login;
  